<template>
  <div>
    <ul class="bottom-inner" >
      <li v-for="(item, index) in xftjxw" 
        :key="index">{{item.day}} 
        <img src="../img/xing.png" alt="" v-if="index > 0 && index <xftjxw.length - 1">
        <div v-if="item && item.children" class="box">
          <div class="i-bottom" v-for="(nItem, nIndex) in item.children" :key="nIndex">
            <img src="../img/icon.png" alt="">
            <span>{{nItem.btn}}</span>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    props: {
      params: {
        type: Array,
        default() {
          return []
        }
      }
    },
    data() {
      return {
        xftjxw: []
      }
    },
    mounted() {
      this.getTime();
    },
    methods: {
      getTime() {
        let now = new Date();
        let year = now.getFullYear();
        let month = now.getMonth() + 1;
        let day = now.getDate()
        let startTime = year-3 + '年' + month + '月'; // 2017
        let endTime = year + '年' + month + '月'; // 2020
        let timeList = []

        // 2018年  sort是排序字段（根据农历日期格式20200203）
        let middleTime = year - 2 + '年'+ '国庆' // 2018年国庆+春节
        let middleTime1 = year - 2 + '年'+ '春节' // 2018年国庆+春节
        let chunj1 = year-2 + '0101' // 春节（农历）的时间
        timeList.push({day: middleTime1, sort: chunj1})
        timeList.push({day: middleTime, sort: this.GetCurrentDateTime(year - 2 , 10, 1)})

        // 2019年
        let middleTime2 = year - 1 + '年'+ '国庆' // 2019年国庆+春节
        let middleTime3 = year - 1 + '年'+ '春节' // 2019年国庆+春节
        let chunj2 = year-1 + '0101' // 春节（农历）的时间
        timeList.push({day: middleTime3, sort: chunj2})
        timeList.push({day: middleTime2, sort:  this.GetCurrentDateTime(year - 1 , 10, 1)})

        // 2017年 
        let strGuoQ = this.GetCurrentDateTime(year-3, 10, 1)  // 当前2017年国庆的农历
        let strChunjN = year-3 + '0101' // 当前2017年春节（农历）的时间
        let strNl = this.GetCurrentDateTime(year-3,month,day) // 当前2017年（农历）的时间

        // 2020年 
        let strNowGuoQ = this.GetCurrentDateTime(year, 10, 1)  // 当前2020年国庆的农历
        let strNowChunjN = year + '0101' // 当前2020年春节（农历）的时间
        let strNowNl = this.GetCurrentDateTime(year,month,day) // 当前2020年（农历）的时间

        // 2017年 某月的时间戳>国庆时间戳  则不包含国庆
        if(!(strChunjN<strNl)) {
          timeList.push({day: year-3 + '年春节', sort: strChunjN})
        }

        if (!(strNl > strGuoQ)) {
          timeList.unshift({day: year-3 + '年国庆', sort: this.GetCurrentDateTime(year-3, 10, 1)})
        }
        

        // 2020年 某月的时间戳>国庆时间戳  则包含国庆
        if (!(strNowNl < strNowChunjN)) {
          timeList.push({day: year + '年春节', sort: strNowChunjN})
        }

        if (!(strNowNl < strNowGuoQ)) {
          timeList.push({day: year + '年国庆', sort: this.GetCurrentDateTime(year, 10, 1)})
        }
        
        timeList.unshift({day: startTime, sort: strNl})
        timeList.push({day: endTime, sort: strNowNl})
        this.xftjxw = timeList.splice(0);
      },
      GetCurrentDateTime(year,month,day) {
        var CalendarData=new Array(100);
        var madd=new Array(12);
        var numString="一二三四五六七八九十";
        var monString="正二三四五六七八九十冬腊";
        var cYear,cMonth,cDay,TheDate;
        CalendarData = new Array(0xA4B,0x5164B,0x6A5,0x6D4,0x415B5,0x2B6,0x957,0x2092F,0x497,0x60C96,0xD4A,0xEA5,0x50DA9,0x5AD,0x2B6,0x3126E, 0x92E,0x7192D,0xC95,0xD4A,0x61B4A,0xB55,0x56A,0x4155B, 0x25D,0x92D,0x2192B,0xA95,0x71695,0x6CA,0xB55,0x50AB5,0x4DA,0xA5B,0x30A57,0x52B,0x8152A,0xE95,0x6AA,0x615AA,0xAB5,0x4B6,0x414AE,0xA57,0x526,0x31D26,0xD95,0x70B55,0x56A,0x96D,0x5095D,0x4AD,0xA4D,0x41A4D,0xD25,0x81AA5,0xB54,0xB6A,0x612DA,0x95B,0x49B,0x41497,0xA4B,0xA164B, 0x6A5,0x6D4,0x615B4,0xAB6,0x957,0x5092F,0x497,0x64B, 0x30D4A,0xEA5,0x80D65,0x5AC,0xAB6,0x5126D,0x92E,0xC96,0x41A95,0xD4A,0xDA5,0x20B55,0x56A,0x7155B,0x25D,0x92D,0x5192B,0xA95,0xB4A,0x416AA,0xAD5,0x90AB5,0x4BA,0xA5B, 0x60A57,0x52B,0xA93,0x40E95);
        madd[0]=0;
        madd[1]=31;
        madd[2]=59;
        madd[3]=90;
        madd[4]=120;
        madd[5]=151;
        madd[6]=181;
        madd[7]=212;
        madd[8]=243;
        madd[9]=273;
        madd[10]=304;
        madd[11]=334;
        
        function GetBit(m,n){
          return (m>>n)&1;
        }
        function e2c(){
          TheDate= (arguments.length!=3) ? new Date() : new Date(arguments[0],arguments[1],arguments[2]);
          var total,m,n,k;
          var isEnd=false;
          var tmp=TheDate.getYear();
          if(tmp<1900){
            tmp+=1900;
          }
          total=(tmp-1921)*365+Math.floor((tmp-1921)/4)+madd[TheDate.getMonth()]+TheDate.getDate()-38;
        
          if(TheDate.getYear()%4==0&&TheDate.getMonth()>1) {
            total++;
          }
          for(m=0;;m++){
            k=(CalendarData[m]<0xfff)?11:12;
            for(n=k;n>=0;n--){
              if(total<=29+GetBit(CalendarData[m],n)){
                isEnd=true; break;
              }
              total=total-29-GetBit(CalendarData[m],n);
            }
            if(isEnd) break;
          }
          cYear=1921 + m;
          cMonth=k-n+1;
          cDay=total;
          if(k==12){
            if(cMonth==Math.floor(CalendarData[m]/0x10000)+1){
              cMonth=1-cMonth;
            }
            if(cMonth>Math.floor(CalendarData[m]/0x10000)+1){
              cMonth--;
            }
          }

          if (cMonth < 10) {
            cMonth = '0'+ cMonth;
          }
          if (cDay < 10) {
            cDay = '0'+ cDay;
          }

          return cYear + '' + cMonth + '' +  cDay
        }
        
        function GetLunarDay(solarYear,solarMonth,solarDay){
          if(solarYear<1921){
            return "";
          }else{
            solarMonth = (parseInt(solarMonth)>0) ? (solarMonth-1) : 11;
            return e2c(solarYear,solarMonth,solarDay);
          }
        }
        return GetLunarDay(year,month,day);
      },
      // 数据格式处理
      concatData(newVal) {
        for(let i=0;i<newVal.length;i++) {
          var patt1 = /\d+/g;
          let arr = newVal[i].day.match(patt1);
          newVal[i].sort = this.GetCurrentDateTime(arr[0], arr[1], arr[2])
        }

        for(let i=0;i<this.xftjxw.length;i++) {
            this.xftjxw[i].children = [];
          for (let j=0;j<newVal.length;j++) {
            if (newVal[j].sort > this.xftjxw[i].sort && newVal[j].sort < this.xftjxw[i+1].sort) {
              this.xftjxw[i].children.push(newVal[j])
            }
          }
        }
        this.xftjxw = this.xftjxw.splice(0)

      },
    },
    watch: {
      'params': {
        handler(newVal, oldVal) {
          this.concatData(newVal);
        },
      }
    }
  }
</script>

<style lang="stylus" scoped>
.bottom-inner
  display flex
  font-size 14px
  color #00e4ff
  position relative
  top 87px
  left 86px
  width 1100px
  justify-content space-around
  background url("../img/bg.png") no-repeat center bottom
  background-size 90%
  padding-bottom 10px
  li
    display flex
    flex-direction column
    align-items center
    position relative
    flex 1
    img 
      width 42px
      height 38px
      margin-top 12px
    .i-bottom
      display flex
      flex-direction column
      align-items center
      flex-shirnk 0
      // margin-left -124px
      // margin-top -8px
      position relative
      img 
        width 23px
        height 16px
        margin-bottom 5px
    .box
      display flex
      position absolute
      bottom -31px
      left 81px
      width 85%
      justify-content space-around
</style>