<template>
  <div class="wrapper">
    <div class="content">
      <div class="w-left">
				<ctitle :title="'基本信息'"></ctitle>
        <div class="inner">
          <Information :params='jbxx'></Information>
        </div>
      </div>
      <div class="w-right">
        <div class="r-top">
          <div class="top-new">
            <ctitle :title="'信访行为分析指数'"></ctitle>
            <div class="msg">(*指数计算方式，由信访行为、扬言、过激行为、问题分类等因素通过不同权重配比计算产生)</div>
          </div>
           <div class="inner">
             <LineMonth :params="zsLineParams"  @departmentClick='departmentClick'></LineMonth>
          </div>
          
        </div>
        <div class="r-bottom">
          <ctitle :title="'信访行为统计'"></ctitle>
          <div class="inner">
            <TimeLine :params="list"></TimeLine>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { get_http } from "@/api/index";
  import ctitle from "@/components/title/index";
  import LineMonth from "@/components/line/lineMonth";
  import Information from '../components/information.vue';
  import TimeLine from '../components/timeLine.vue';
  export default {
    name: 'xwgjyj',
    components: {
      ctitle,
      LineMonth,
      Information,
      TimeLine
    },
    data() {
      return {
        xftjxw: [],
        list: [],
        jbxx: {},
        // 信访行为分析指数
				zsLineParams: {
          data: [],
          colors: [
            {
              line: '#1a82f7',
              start: "rgba(181, 127, 182, 0)",
              end: "rgba(181, 127, 182, 0)"
            },
          ],
          
          option: {
            // rollFlag: true,
            splitLineShow: false,
            legendShow: false,
            legenditemWidth: 22,
            legenditemHeight: 4,
            axisLabelShow: true,
            axisTickShow: false,
            axisCategoryName: "月",
            axisYName: "",
            axisLabelSize: 14,
            scales: true,
            axisLabelFontSize: 16,
            yaxisLabelFontSize: 14,
            Tofixed: false,
            smooth: true,
            areaShow: 1,
            smoothMonotone: "none",
            dataZoomshow: false,
            legendIcon: "rect",
            left: "8%",
            right: "15%",
            top: "15%",
            bottom: "10%",
            legendTop: "2%",
            legendRight: "15%",
            yAxisIsNotShow: true,
          },
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          map: {
            "value": "数量",
          },
          xName: "name"
				},
      }
    },
    mounted() {
      this.getAPI(this.year)
    },
    methods: {
      departmentClick(name) {
        // console.error('name', name);
        this.getJbxxAPI(this.year)
      },
      getJbxxAPI(year) {
        // 1.基本信息
        get_http('xfxwfxyjXwgjyjJbxx', {
          year: year
        })
        .then(res => {
          this.jbxx = res.data.data[0];
        })
        .catch(err => {
          console.log(err);
        });
      },
      getAPI(year) {
        
        this.getJbxxAPI(year)
        // 2.信访行为分析指数
        get_http("xfxwfxyjXwgjyjFxzs", {
          year: year
        })
        .then(res => {
          this.zsLineParams.data = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });

        // 3.信访行为统计
        get_http("xfxwfxyjXwgjyjXfxwtj", {
          year: year
        })
        .then(res => {
          this.list = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });

      },

    },
    computed: {
      year() {
        return this.$store.state.year;
      }
    },
    watch: {
      year(val) {
        this.getAPI(val);
      }
    }
  }
</script>

<style lang="stylus" scoped>
.wrapper
  position relative
  top 0.45rem
  height calc(100% - 45px)
  .content
    width 100%
    height 100%
    display flex
    padding .2rem .3rem
    box-sizing border-box
    .w-left
      flex 1
      // .inner
    .w-right
      flex 2
      display flex
      flex-direction column
      .r-top
        flex 1.5
        display flex
        flex-direction column
        .top-new
          display flex
          align-items flex-end
        .inner
          width 100%
          height 4rem
        .msg
          font-size 16px
          color #00e4ff
          text-align right
          padding-right 1.5rem
          margin-bottom 4px
          margin-left 5px
      .r-bottom
        flex 1
        .inner
          width 100%
          height 100%
          // background url("../img/bg.png") no-repeat center 112px
          .bottom-inner
            display flex
            font-size 14px
            color #94e4f2
            position relative
            top 87px
            left 86px
            width 1100px
            justify-content space-around
            background url("../img/bg.png") no-repeat center bottom
            background-size 90%
            padding-bottom 10px
            li
              display flex
              flex-direction column
              align-items center
              position relative
              flex 1
              img 
                width 42px
                height 38px
                margin-top 12px
              .i-bottom
                display flex
                flex-direction column
                align-items center
                flex-shirnk 0
                // margin-left -124px
                // margin-top -8px
                position relative
                img 
                  width 23px
                  height 16px
                  margin-bottom 5px
              .box
                display flex
                position absolute
                bottom -31px
                left 81px
                width 85%
                justify-content space-around

</style>